<template>
    <div>
        <div class="chapter">
            <div class="chapter-head">
                <van-icon name="arrow-left" @click="routeBack" />
            </div>
            <div class="chapter-middle">
                <span class="chapter-list">共{{ chapterList.length }}章</span>
                <div class="chpater-reverse" @click="reverse">
                    <span v-if="sort">正序</span>
                    <span v-else>倒序</span>
                    <img src="../assets/arrows.svg" />
                </div>
            </div>
            <ul class="chapter-footer">
                <ChapterItem
                    v-for="(chapter, index) in currentChapterList"
                    :key="index"
                    :item="chapter"
                    :currentIndex="index"
                ></ChapterItem>
            </ul>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import types from "@/store/types.js";
import ChapterItem from "@/components/ChapterItem.vue";
export default {
    name: "BookChapter",
    data() {
        return {
            sort: false,
            currentChapterList: [],
        };
    },
    components: {
        ChapterItem,
    },
    computed: {
        ...mapState({
            chapterList: (state) => state.chapterList,
        }),
    },
    methods: {
        ...mapActions([types.GET_BOOK_CHAPTER]),
        routeBack() {
            this.$router.back();
        },
        reverse() {
            this.sort = !this.sort;
            this.currentChapterList = this.currentChapterList.reverse();
        },
    },
    watch: {
        chapterList(val) {
            this.currentChapterList = val;
        },
    },
    created() {
        this[types.GET_BOOK_CHAPTER]({
            view: "summary",
            book: this.$route.params.id,
        });
    },
    beforeEnter: (to, from, next) => {},
};
</script>

<style lang="less" scoped>
.chapter {
    padding: 10px;
    .chapter-head {
        text-align: left;
        font-size: 18px;
    }
    .chapter-middle {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: rgba(0, 0, 0, 0.1);
        color: #666;
        font-size: 13px;
        margin-top: 10px;
        padding: 2px 5px;
        .chpater-reverse {
            display: flex;
            align-items: center;
            img {
                width: 12px;
            }
        }
    }
}
</style>
