<template>
    <li>
        <p
            class="chapter-item"
            :class="{ prevent: item.isVip }"
            @click="routeTo"
        >
            <span class="chapter-title">{{ item.title }}</span>
            <van-icon class="chapter-lock" name="lock" v-if="item.isVip" />
        </p>
    </li>
</template>

<script>
export default {
    name: "ChapterItem",
    props: {
        item: Object,
        currentIndex: Number,
    },
    methods: {
        routeTo() {
            if (this.item.isVip) {
                return;
            }
            this.$router.push({
                name: "Read",
                params: {
                    title: this.$route.params.title,
                    id: this.$route.params.id,
                    index: this.currentIndex,
                },
            });
        },
    },
};
</script>

<style lang="less" scoped>
.chapter-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 13px;
    padding: 5px;
    &.prevent {
        color: #999;
    }
    .chapter-title {
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }
    .chapter-lock {
        color: #e4393c;
    }
}
</style>
